body{
    background-color:black;
    color:grey;
}
.hed{
    border-bottom:1px solid #777;
    display:flex;
    gap:20px;
    align-items:center;
    &_img{
        height:70px;
    }
    &_dis{
        color:#777;
        font-size:50px;
        font-weight:bold;
    }
}
.contactIndex{
    min-height:80vh;
    margin-top:10px;
    display:flex;
    flex-direction:column;
    gap:10px;
    &_row{
        display:flex;
        justify-content:center;
        gap:10px;
       
        div{
             flex-basis:400px;
        }
    }
}
.addContact{
    border:1px solid grey;
    padding:5px;
    display:flex;
    flex-direction:column;
    gap:5px;
    &_title{
        color:grey;
        text-align:center;
    }
    &_row{
        display:flex;
        gap:5px;
    }
}
.contact{
    background-color:rgb(1, 20, 20);
    height:80px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    gap:10px;
    border:1px solid grey;
    border-radius:3px;
    padding:5px;
    margin-top:5px;
    img{
        height:70px;
    }
    &_descr{
        display:flex;
        flex-direction:column;
    }
}
.favorit{
    background-color:rgb(1, 20, 20);
    padding:10px;
    &_title{
        font-size:20px;
        text-align:center;
    }
}
.general{
    background-color:rgb(1, 20, 20);
    padding:10px;
    &_title{
        font-size:20px;
        text-align:center;
    }
}